import { createSlice } from '@reduxjs/toolkit';
import { v4 as uuid } from 'uuid';

const initialState = [
  // {
  //     message: '',
  //     options: {
  //         key: '',
  //         variant: '',
  //         action : (key) => {}
  //     }
  //     key: '',
  // }
];

const slice = createSlice({
  name: 'alert',
  initialState,
  reducers: {
    addAlert(state, action) {
      state.push(action.payload);
    },
    removeAlert(state, action) {
      return state.filter((n) => n.key !== action.payload);
    },
    removeAllAlerts(state, action) {
      return state.map((n) => (action.payload.dismissAll || n.key === action.payload.key ? { ...n, dismissed: true } : { ...n }));
    }
  }
});

export const reducer = slice.reducer;

export const addErrorAlert = (err) => async (dispatch) => {
  dispatch(
    addAlert({
      message: err.response.data.data || err.response.data.message,
      options: { variant: 'error' }
    })
  );
};

export const addAlert = (alert) => async (dispatch) => {
  const key = alert.options && alert.options.key;

  dispatch(
    slice.actions.addAlert({
      ...alert,
      key: key || new Date().getTime() + Math.random()
    })
  );
};

export const removeAlert = (key) => async (dispatch) => {
  dispatch(slice.actions.removeAlert(key));
};

export const removeAlerts = (key) => async (dispatch) => {
  dispatch(
    slice.actions.removeAllAlerts({
      dismissAll: !key, // dismiss all if no key has been defined
      key
    })
  );
};

export default slice;
