import React from 'react';
import { Container, Toolbar, Drawer, Box, Hidden } from '@mui/material';
import { styled, css } from '@mui/material/styles';
import Header from './Header';
import NavBar from './NavBar';

const drawerWidth = 230;

const StyledDrawer = styled(Drawer)(
  ({ theme }) => css`
    & .MuiDrawer-paper {
      width: ${drawerWidth}px;
      background: #f9f9f9;
      border: none;
    }
  `
);

const Layout = ({ children }) => {
  const [isMobileNavOpen, setMobileNavOpen] = React.useState(false);

  return (
    <Box>
      <Header />
      <Hidden xlUp implementation="css">
        <StyledDrawer anchor="left" variant="temporary" open={isMobileNavOpen} onClose={() => setMobileNavOpen(false)}>
          <Toolbar />
          <NavBar />
        </StyledDrawer>
      </Hidden>
      <Hidden lgDown implementation="css">
        <StyledDrawer anchor="left" open variant="persistent">
          <Toolbar />
          <NavBar />
        </StyledDrawer>
      </Hidden>
      <Box sx={{ paddingTop: { xs: 3 }, paddingLeft: { xs: 0, lg: `${drawerWidth}px}` } }}>
        <Toolbar />
        <Container>{children}</Container>
      </Box>
    </Box>
  );
};

export default Layout;
