import React from 'react';
import { Container, Link, Box, Button, Menu, MenuItem, AppBar, Toolbar } from '@mui/material';
import Logo from 'src/components/Logo';
import { Link as RouterLink } from 'react-router-dom';

const style = {
  logo: {
    width: '100%',
    maxWidth: '170px'
  },
  menuItem: {
    padding: '0px 10px',
    fontWeight: 'normal',
    fontSize: '16px',
    lineHeight: '24px',
    textDecoration: 'none'
  }
};
const logo = require('src/assets/images/logo.jpg');

const menus = [
  { label: 'What is Vleppo', link: '#' },
  { label: 'Vleppo Business', link: '#' },
  { label: 'Partnerships', link: '#' },
  { label: 'Insights', link: '#' },
  { label: 'Download', link: '/download' }
];

const Header = () => {

  const [anchorEl, setAnchorEl] = React.useState(null);

  const open = Boolean(anchorEl);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  
  const handleClose = () => {
    setAnchorEl(null);
  };
  return (
    <Container maxWidth="lg">
        <AppBar color="transparent" position="relative" sx={{ border: 'none', boxShadow: 'none' }}>
          <Toolbar>
            <Link component={RouterLink} to="/" style={{ textDecoration: 'none' }}>
              <Logo />
            </Link>
            <Box sx={{ flexGrow: 1 }} />
            <Box>
              <Button
                aria-controls={open ? 'basic-menu' : undefined}
                aria-haspopup="true"
                aria-expanded={open ? 'true' : undefined}
                onClick={handleClick}
              >
                Block Explorer
              </Button>
              <Menu
                anchorEl={anchorEl}
                open={open}
                onClose={handleClose}
              >
                <MenuItem component={RouterLink} to="/block-explorer">Blocks</MenuItem>
                <MenuItem component={RouterLink} to="/block-explorer/transactions">Transactions</MenuItem>
              </Menu>
              {menus.map((menu, index) => {
                return (
                  <Link component={RouterLink} to={menu.link} style={style.menuItem} key={index}>
                    {menu.label}
                  </Link>
                );
              })}
            </Box>
          </Toolbar>
        </AppBar>
      </Container>
  )
}

export default Header;
