import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import api from 'src/utils/api';

const initialState = {
  loading: false,
  error: '',
  blocks: [],
  blocksCount: 0,
  blocksPerPage: 20,
  blocksPage: 0,
  currentBlock: null
};

export const loadBlocks = createAsyncThunk('blocks/load', async (params, action) => {
  try {
    const hash = `&hash=${params.hash}`
    const { data } = await api.get(`/block-explorer/block?page=${params.page + 1}${params.hash !== undefined ? hash : ''}`);
    return data.data;
  } catch (err) {
    return action.rejectWithValue(err.message);
  }
});

export const retrieveBlock = createAsyncThunk('blocks/retrieve', async (hash, action) => {
  try {
    const { data } = await api.get(`/block-explorer/block/${hash}`);
    return data.data;
  } catch (err) {
    return action.rejectWithValue(err.message);
  }
});

const slice = createSlice({
  name: 'blocks',
  initialState,
  reducers: {},
  extraReducers: {
    [loadBlocks.pending]: (state) => {
      state.loading = true;
    },
    [loadBlocks.fulfilled]: (state, action) => {
      state.loading = false;
      state.blocks = action.payload.content;
      state.blocksCount = action.payload.total_elements;
      state.blocksPerPage = action.payload.size;
      state.blocksPage = action.payload.number;
    },
    [loadBlocks.rejected]: (state) => {
      state.loading = false;
      state.error = state.payload;
    },
    [retrieveBlock.pending]: (state) => {
      state.loading = true;
    },
    [retrieveBlock.fulfilled]: (state, action) => {
      state.loading = false;
      state.currentBlock = action.payload;
    },
    [retrieveBlock.rejected]: (state) => {
      state.loading = false;
      state.error = state.payload;
    }
  }
});

export const reducer = slice.reducer;

export default slice;
