import { combineReducers } from '@reduxjs/toolkit';
// Shared
import { reducer as alertReducer } from 'src/store/slices/alert';
import { reducer as authReducer } from 'src/store/slices/auth';
import { reducer as settingReducer } from 'src/store/slices/setting';
// Slate
import { reducer as contractReducer } from 'src/store/slices/slate/contract';
import { reducer as blockReducer } from 'src/store/slices/slate/block';
import { reducer as transactionReducer } from 'src/store/slices/slate/transaction';

const rootReducer = combineReducers({
  slate: combineReducers({
    contract: contractReducer,
    block: blockReducer,
    transaction: transactionReducer
  }),
  setting: settingReducer,
  alert: alertReducer,
  auth: authReducer
});

export default rootReducer;
