import React, { useEffect } from 'react';
import './App.css';
// import { BrowserRouter as Router } from 'react-router-dom';
import { Router, HashRouter } from 'react-router-dom';
import { SnackbarProvider } from 'notistack';
import { ThemeProvider } from '@mui/material/styles';
import { darkTheme, lightTheme } from 'src/theme/index';
import CssBaseline from '@mui/material/CssBaseline';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import routes, { renderRoutes } from './routes';
import useAlert from './hooks/useAlert';
import setAuthToken from './utils/setAuthToken';
import { loadUser, logout } from './store/slices/auth';
import { useDispatch, useSelector } from './store';
import { history } from './utils/history';

console.log('process', process.env)
const RouterElement = typeof process.env.REACT_APP_HAHSROUTER !== 'undefined' ? HashRouter : Router

const App = () => {
  const dispatch = useDispatch();
  const { theme } = useSelector(state => state.setting)

  useEffect(() => {
    if (localStorage.token) {
      setAuthToken(localStorage.token);
      dispatch(loadUser());
    }
  }, []);

  return <LocalizationProvider dateAdapter={AdapterDateFns}>
    <ThemeProvider theme={theme === 'light' ? lightTheme : darkTheme}>
      <CssBaseline />
      <SnackbarProvider dense>
        <AppContainer />
      </SnackbarProvider>
    </ThemeProvider>
  </LocalizationProvider>
};

const AppContainer = () => {
  useAlert();

  return <RouterElement history={history}>{renderRoutes(routes)}</RouterElement>
}

export default App;
