import React from 'react';
import { List, Box, ListItemIcon, ListItemText, Collapse, ListItemButton } from '@mui/material';
import { Article, Send, MarkAsUnread, DataUsage, ExpandMore, ExpandLess } from '@mui/icons-material';
import { styled, css } from '@mui/material/styles';
import { NavLink } from 'react-router-dom';

const StyledListItemIcon = styled(ListItemIcon)(
  ({ theme }) => css`
    min-width: 32px;
  `
);

const SideBar = () => {
  const [open, setOpen] = React.useState(true);

  const handleClick = () => {
    setOpen(!open);
  };

  return (
    <Box>
      <List
        sx={{
          [`& .active`]: {
            '& .MuiSvgIcon-root': {
              color: 'primary.main'
            },
            '& .MuiTypography-root': {
              color: 'primary.main'
            }
          }
        }}
      >
        <ListItemButton onClick={handleClick}>
          <StyledListItemIcon>
            <Article />
          </StyledListItemIcon>
          <ListItemText primary="Contract" />
          {open ? <ExpandLess /> : <ExpandMore />}
        </ListItemButton>
        <Collapse in={open} timeout="auto" unmountOnExit>
          <ListItemButton>
            <StyledListItemIcon />
            <ListItemText primary="Contract Template" />
          </ListItemButton>
          <ListItemButton component={NavLink} to="/slate/contract/add">
            <StyledListItemIcon />
            <ListItemText primary="Add New" />
          </ListItemButton>
          <ListItemButton>
            <StyledListItemIcon />
            <ListItemText primary="Categories" />
          </ListItemButton>
        </Collapse>

        <ListItemButton component={NavLink} to="/slate/sent-contracts">
          <StyledListItemIcon>
            <Send />
          </StyledListItemIcon>
          <ListItemText primary="Send Contract" />
        </ListItemButton>

        <ListItemButton component={NavLink} to="/slate/received-contracts">
          <StyledListItemIcon>
            <MarkAsUnread />
          </StyledListItemIcon>
          <ListItemText primary="Receive Contract" />
        </ListItemButton>
        <ListItemButton component={NavLink} to="/c">
          <StyledListItemIcon>
            <DataUsage />
          </StyledListItemIcon>
          <ListItemText primary="Contract Usages" />
        </ListItemButton>
        <ListItemButton component={NavLink} to="/slate/contact">
          <StyledListItemIcon>
            <DataUsage />
          </StyledListItemIcon>
          <ListItemText primary="Contract us" />
        </ListItemButton>
      </List>
    </Box>
  );
};

export default SideBar;
