import { createSlice } from '@reduxjs/toolkit';
import api from 'src/utils/api';
import { addErrorAlert, addAlert } from './alert';
import { history } from 'src/utils/history';

const initialState = {
  loading: true,
  isAuthenticated: null,
  user: null,
  token: localStorage.token
};

const slice = createSlice({
  name: 'auth',
  initialState,
  reducers: {
    loadUser(state, action) {
      return {
        ...state,
        loading: false,
        isAuthenticated: true,
        user: action.payload
      };
    },
    login(state, action) {
      return {
        ...state,
        loading: false,
        isAuthenticated: true,
        token: action.payload
      };
    },
    register(state, action) {
      return {
        ...state,
        loading: false,
        isAuthenticated: true,
        token: action.payload
      };
    },
    verifyAccount(state, action) {
      return {
        ...state,
        loading: false,
        isAuthenticated: true,
        token: action.payload
      };
    },
    logout(state, action) {
      return {
        ...state,
        loading: false,
        isAuthenticated: false,
        user: null,
        token: null
      };
    }
  }
});

export const loadUser = () => async (dispatch) => {
  try {
    const { data } = await api.get('/user/me');
    dispatch(slice.actions.loadUser(data.data));
  } catch (err) {
    dispatch(logout());
  }
};

export const login = (loginId, password) => async (dispatch) => {
  try {
    const { data } = await api.post('/auth/login/pw', {
      login_id: loginId,
      password: password
    });
    dispatch(slice.actions.login(data.data));
    dispatch(loadUser());
  } catch (err) {
    dispatch(addErrorAlert(err));
  }
};

export const register = (params) => async (dispatch) => {
  try {
    const { data } = await api.post('/user/sign-up', {
      address: params.address,
      // admin: true,
      country: params.country,
      dob: params.dob,
      email: params.email,
      first_name: params.first_name,
      last_name: params.last_name,
      phone: params.phone,
      phone_country_code: params.phone_country_code,
      state: params.state,
      username: params.username,
      password: params.password
    });
    history.push('/confirm-account');
    // dispatch(slice.actions.register(data.data));
    // dispatch(loadUser());
  } catch (err) {
    dispatch(addErrorAlert(err));
  }
};

export const verifyAccount = (token) => async (dispatch) => {
  try {
    const { data } = await api.post(`/user/sign-up/verify/${token}`);
    // window.location.href="http://localhost:4200/vleppo/confirm-account"
    // dispatch(slice.actions.register(data.data));
    // dispatch(loadUser());
  } catch (err) {
    dispatch(addErrorAlert(err));
  }
};

export const logout = () => async (dispatch) => {
  try {
    dispatch(slice.actions.logout());
    const system = history.location.pathname.split('/')[1];
    history.push(`/${system}/login`);
  } catch (err) {
    dispatch(addAlert(err));
  }
};

export const reducer = slice.reducer;
export default slice;
