import React from 'react';
import { Box, Typography, Avatar, IconButton, Menu, MenuItem, AppBar, Toolbar, Hidden, Link, SvgIcon, TextField, InputAdornment, Chip, Badge } from '@mui/material';
import { Menu as MenuIcon, Search, NotificationsOutlined } from '@mui/icons-material';
import { Link as RouterLink } from 'react-router-dom';
import { useDispatch } from 'src/store';
import { logout } from 'src/store/slices/auth';
import Logo from 'src/components/Logo';
import { styled, css } from '@mui/material/styles';

const SearchTextField = styled(TextField)(
  () => css`
    width: 100%;
    max-width: 600px;
    & .MuiOutlinedInput-root {
      border-radius: 100px;
    }
  `
);

const Dot = styled(Box)(
  ({ theme }) => css`
    width: 8px;
    height: 8px;
    background: ${theme.palette.success.main};
    margin-right: 2px;
    border-radius: 4px;
  `
);

const Header = ({ title }) => {
  const dispatch = useDispatch();

  const [anchorEl, setAnchorEl] = React.useState(null);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleLogout = () => {
    handleClose();
    dispatch(logout());
  };

  return (
    <AppBar position="fixed" sx={{ zIndex: (theme) => theme.zIndex.drawer + 1, background: '#FFF' }}>
      <Toolbar>
        <Box width="100%" display="flex" justifyContent="space-between">
          <Hidden implementation="css" lgUp>
            <IconButton>
              <SvgIcon>
                <MenuIcon />
              </SvgIcon>
            </IconButton>
          </Hidden>
          <Hidden implementation="css" lgDown>
            <Link component={RouterLink} to="/slate">
              <Logo />
            </Link>
          </Hidden>
          <SearchTextField
            variant="outlined"
            placeholder="Search"
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <Search />
                </InputAdornment>
              )
            }}
          />
          <Box display="flex" alignItems="center">
            <Box display="flex" alignItems="center">
              <Dot>&nbsp;</Dot>
              <Typography mr={4} color="success.main">
                Online
              </Typography>
            </Box>

            <Box component="span" mr={2}>
              <Chip label="99" color="primary" onClick={() => {}} />
            </Box>
            <Box component="span" mr={2}>
              <Chip label="Get free coins" color="error" onClick={() => {}} />
            </Box>
            <Box component="span" mr={2}>
              <IconButton>
                <Badge badgeContent={4} color="primary">
                  <NotificationsOutlined color="action" />
                </Badge>
              </IconButton>
            </Box>

            <IconButton onClick={handleClick}>
              <Avatar src="https://mui.com/static/images/avatar/3.jpg" />
            </IconButton>

            <Menu anchorEl={anchorEl} open={Boolean(anchorEl)} onClose={handleClose}>
              <MenuItem onClick={handleClose}><Link component={RouterLink} to="/slate/profile">Profile</Link></MenuItem>
              <MenuItem onClick={handleClose}>My account</MenuItem>
              <MenuItem onClick={handleLogout}>Logout</MenuItem>
            </Menu>
          </Box>
        </Box>
      </Toolbar>
    </AppBar>
  );
};

export default Header;
