import React from 'react';
import { useDispatch, useSelector } from 'src/store';
import { removeAlert } from 'src/store/slices/alert';
import { useSnackbar } from 'notistack';

let displayed = [];

const useAlert = () => {
  const dispatch = useDispatch();
  const alerts = useSelector((state) => {
    return state.alert;
  });
  const { enqueueSnackbar, closeSnackbar } = useSnackbar();

  const storeDisplayed = (id) => {
    displayed = [...displayed, id];
  };

  const removeDisplayed = (id) => {
    displayed = [...displayed.filter((n) => n !== id)];
  };

  React.useEffect(() => {
    alerts.forEach(({ key, message, options = {}, dismissed = false }) => {
      if (dismissed) {
        // dismiss snackbar using notistack
        closeSnackbar(key);
        return;
      }

      // do nothing if snackbar is already displayed
      if (displayed.includes(key)) return;

      // display snackbar using notistack
      enqueueSnackbar(message, {
        key,
        ...options,
        onClose: (event, reason, myKey) => {
          if (options.onClose) {
            options.onClose(event, reason, myKey);
          }
        },
        onExited: (event, myKey) => {
          // remove this snackbar from redux store
          dispatch(removeAlert(myKey));
          removeDisplayed(myKey);
        }
      });

      // keep track of snackbars that we've displayed
      storeDisplayed(key);
    });
  }, [alerts, closeSnackbar, enqueueSnackbar, dispatch]);
};

export default useAlert;
