import { Redirect } from 'react-router-dom';
import useAuth from 'src/hooks/useAuth';
import { useLocation } from 'react-router-dom';
import LoadingScreen from './LoadingScreen';

const AuthGuard = ({ children }) => {
  const { isAuthenticated } = useAuth();
  const location = useLocation();
  const redirect = location.pathname + (location.search === '' ? encodeURIComponent(location.search) : '');

  if (isAuthenticated === null) {
    return <LoadingScreen circle />;
  }

  if (!isAuthenticated) {
    const system = location.pathname.split('/')[1];
    const redirectUrl = redirect ? `/${system}/login?returnUrl=${redirect}` : `${system}/login`;
    return <Redirect to={redirectUrl} />;
  }

  return <>{children}</>;
};

export default AuthGuard;
