import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import api from 'src/utils/api';

const initialState = {
  loading: false,
  error: '',
  preferences: null,
  theme: localStorage.getItem('CURRENT_THEME') || 'light'
};

const saveCurrentTheme = (theme) => {
  localStorage.setItem('CURRENT_THEME', theme)
}

export const setTheme = createAsyncThunk('theme/set', async (theme, action) => {
  saveCurrentTheme(theme)
  return theme
});

export const getPreferences = createAsyncThunk('preferences/get', async (params, action) => {
  const { data } = await api.get(`/user/preference`);
  // translate to readable prefenreces
  return {
    lang: data.data.lang,
    themes: data.data.themes === 1 ? 'light' : 'dark',
    notification: {
      like_comment: data.data.notification.at(0) === '1',
      reply_comment: data.data.notification.at(1) === '1',
      comment: data.data.notification.at(2) === '1',
      ban: data.data.notification.at(3) === '1',
      asset_transfer: data.data.notification.at(4) === '1'
    },
    privacy: {
      display_email: data.data.privacy_setting.at(0) === '1',
      display_phone: data.data.privacy_setting.at(1) === '1',
    }
  }
})

export const updatePreferences = createAsyncThunk('preferences/update', async (payload, action) => {
  const postBody = {
    language: payload.lang,
    theme: payload.themes === 'light' ? 1 : 2,
    notification: `${payload.notification.like_comment ? 1 : 0}${payload.notification.reply_comment ? 1 : 0}${payload.notification.comment ? 1 : 0}${payload.notification.ban ? 1 : 0}${payload.notification.asset_transfer ? 1 : 0}`,
    privacy_setting: `${payload.privacy.display_email ? 1 : 0}${payload.privacy.display_phone ? 1 : 0}`
  }
  await api.put(`/user/preference`, postBody);
  action.dispatch(setTheme(payload.themes))
  return payload
})

const slice = createSlice({
  name: 'setting',
  initialState,
  reducers: {},
  extraReducers: {
    [setTheme.pending]: (state) => {
      state.loading = true;
    },
    [setTheme.fulfilled]: (state, action) => {
      state.loading = false;
      state.theme = action.payload;
    },
    [setTheme.rejected]: (state) => {
      state.loading = false;
      state.error = state.payload;
    },
    [getPreferences.pending]: (state) => {
      state.loading = true;
    },
    [getPreferences.fulfilled]: (state, action) => {
      state.loading = false;
      state.preferences = action.payload;
    },
    [getPreferences.rejected]: (state) => {
      state.loading = false;
      state.error = state.payload;
    },
    [updatePreferences.pending]: (state) => {
      state.loading = true;
    },
    [updatePreferences.fulfilled]: (state, action) => {
      state.loading = false;
      state.preferences = action.payload;
    },
    [updatePreferences.rejected]: (state) => {
      state.loading = false;
      state.error = state.payload;
    }
  }
});

export const reducer = slice.reducer;

export default slice;
