import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import api from 'src/utils/api';

const initialState = {
  loading: false,
  error: '',
  transactions: [],
  transactionsCount: 0,
  transactionsPerPage: 20,
  transactionsPage: 0,
  currentTransaction: null
};

export const loadTransactions = createAsyncThunk('transactions/load', async (params, action) => {
  try {
    const hash = `&hash=${params.hash}`
    const { data } = await api.get(`/block-explorer/transaction?page=${params.page + 1}${params.hash !== undefined ? hash : ''}`);
    return data.data;
  } catch (err) {
    return action.rejectWithValue(err.message);
  }
});

export const retrieveTransaction = createAsyncThunk('transactions/retrieve', async (id, action) => {
  try {
    const { data } = await api.get(`/block-explorer/transaction/${id}`);
    return data.data;
  } catch (err) {
    return action.rejectWithValue(err.message);
  }
});

const slice = createSlice({
  name: 'transactions',
  initialState,
  reducers: {},
  extraReducers: {
    [loadTransactions.pending]: (state) => {
      state.loading = true;
    },
    [loadTransactions.fulfilled]: (state, action) => {
      state.loading = false;
      state.transactions = action.payload.content;
      state.transactionsCount = action.payload.total_elements;
      state.transactionsPerPage = action.payload.size;
      state.transactionsPage = action.payload.number;
    },
    [loadTransactions.rejected]: (state) => {
      state.loading = false;
      state.error = state.payload;
    },
    [retrieveTransaction.pending]: (state) => {
      state.loading = true;
    },
    [retrieveTransaction.fulfilled]: (state, action) => {
      state.loading = false;
      state.currentTransaction = action.payload;
    },
    [retrieveTransaction.rejected]: (state) => {
      state.loading = false;
      state.error = state.payload;
    }
  }
});

export const reducer = slice.reducer;

export default slice;
